<script>
import { Bar }  from 'vue-chartjs';

export default {
    extends: Bar,
    props: [ 'callinfo' ],
    methods: {
        daysInMonth(year, month) {
            return new Date(year, month, 0).getDate();
        },
        dayList(days) {
            const daysArray = [];
            for (let i = 1; i <= days; i += 1 ) {
                daysArray.push(i);
            }
            return daysArray;
        },
    },
    mounted() {
        console.log('Props in Bar');
        console.log(this.callinfo);
        // Create Data Set
        const data = {
            datasets: [
                {
                    label: 'Calling Time (Minutes)',
                    data: [10, 0, 30, 40],
                    // Changes this dataset to become a line
                    type: 'line'
                }
            ],
            labels: this.dayList(this.daysInMonth(2018, 11)),
        }
        const options = {
            responsive: true,
        }
        this.renderChart(data, options);
    },
    updated() {
        console.log('Props in Bar');
        console.log(this.callinfo);
    }
}
</script>
