import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import CallDetails from "./views/CallDetailMain.vue";
import CallDetailsServiceUsage from "./views/CallDetailServiceUsage.vue";
import CallDetailsServiceUsagePerUser from "./views/CallDetailServiceUsagePerUser.vue";
import CallDetailsServiceUsagePerUserInDetail from "./views/CallDetailServiceUsagePerUserInDetail.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
    },
    {
      path: "/about",
      name: "about",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: "about" */ "./views/About.vue"),
    },
    {
      path: "/call-details",
      name: "call-details",
      component: CallDetails,
    },
    {
      path: "/call-details/service-usage",
      name: "call-details-service-usage",
      component: CallDetailsServiceUsage,
    },
    {
      path: "/call-details/service-usage/:caller_id_number",
      name: "call-details-service-usage-per-user",
      component: CallDetailsServiceUsagePerUser,
      props: true,
    },
    {
      path: "/call-details/service-usage/:caller_id_number/detail/:uuid",
      name: "call-details-service-usage-per-user-indetail",
      component: CallDetailsServiceUsagePerUserInDetail,
      props: true,
    },
    {
      path: "/call-details/between-users",
      name: "call-details-between-users",
      component: Home,
    },
    {
      path: "/call-details/trunk-usage",
      name: "call-details-trunk-usage",
      component: Home,
    },
    {
      path: "/call-details/queue-usage",
      name: "call-details-queue-usage",
      component: Home,
    },
  ]
});
