<template>
  <div class="home">
    <h1>รายละเอียดการโทร</h1>
    <h4>ของ {{ callinfo.caller_id_name }}</h4>
    <h5>วันที่ {{ callinfo.call_date }}</h5>
    <call-details :callinfo="this.callinfo"/>
  </div>
</template>

<script>
import CallDetails from '../components/CallDetails';

export default {
  components: {
    CallDetails,
  },
  data() {
    return {
      callinfo: {
        caller_id_name: '',
        caller_id_number: '',
        call_date: '',
      },
    }
  },
  mounted() {
    this.callinfo = this.$route.params.callinfo;
    window.history.pushState("", "", `/call-details/service-usage/${this.$route.params.callinfo.caller_id_number}/detail/${this.$route.params.callinfo.uuid}`);
  },
}
</script>

