<template>
  <div class="home">
    <h1>สถิติการใช้บริการของ {{ user.caller_id_name }}</h1>
    <call-list :calllist="callList" />
  </div>
</template>

<script>
import CallList from '../components/presentations/CallList';
import CallData from '../sample/CallData.json';

export default {
  mounted() {
    this.user = this.$route.params.userinfo;
    window.history.pushState("", "", `/call-details/service-usage/${this.$route.params.userinfo.caller_id_number}`);
    const callListKey = Object.keys(CallData[this.user.caller_id_number]);
    this.callList = callListKey.map(callUUID => {
      let newCallDetail = CallData[this.user.caller_id_number][callUUID];
      newCallDetail.call_date = new Date(newCallDetail.start_epoch * 1000).toDateString();
      newCallDetail.call_time = new Date(newCallDetail.start_epoch * 1000).toTimeString();
      newCallDetail.call_duration = (newCallDetail.end_epoch - newCallDetail.start_epoch);
      return newCallDetail;
    })
  },
  data() {
    return {
      user: {

      },
      callList: [],
    }
  },
  components: {
    CallList,
  }
}
</script>

